import React from 'react';
import Home from './components/Home'
// import Contact from './components/Contact';
import {Dropdown, DropdownButton} from 'react-bootstrap'
import Logo from './img/logo500.png'
import FacebookPage from './components/FacebookPage';


class App extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      language:  'ja'
    }
  }

  setLanguage = (lang) => {
    this.setState({
      language: lang
    })
  }

  render() {
    const language = this.state.language
    const dropdown = <DropdownButton variant="secondary" title="Language" id="language-select">
      <Dropdown.Item as="button" onSelect={() => this.setLanguage('ja')} >日本語</Dropdown.Item>
      <Dropdown.Item as="button" onSelect={() => this.setLanguage('en')} >English</Dropdown.Item>
    </DropdownButton>
    return (
      <div className="main">
        <header>
          <img src={Logo} alt="QUE" />
          {dropdown}
        </header>
        <Home language={language}/>
        {/* <Contact language={language}/> */}
        <FacebookPage />
        <footer><p>Copyright ©︎ 2020 QUE All rights reserved.</p></footer>
      </div>
    )
  }
}

export default App;
